import noData from '@/components/no-data'
import { getAddressList, deleteAddress, BASE_URL } from '@/api'
import { mapMutations } from 'vuex'
export default {
	data() {
		return {
			addressStatus: true,   // 地址状态  true 管理  false 删除
			addressList: [],
			chose: false
		};
	},
	onLoad (options) {
		if(options.chose) {
			this.chose = true;
		}
	},
	onShow () {
		this._getAddressList();
	},
	methods: {
		...mapMutations(['saveDeliveryAddress']),
		choseAddress (item) {
			if(this.chose) {
				this.saveDeliveryAddress(item)
				uni.navigateBack()
			}
		},
		async _getAddressList () {   // 获取地址列表
			uni.showLoading({
				title: '正在加载数据...'
			})
			let result = await getAddressList ({
				token: uni.getStorageSync('token')
			})
			uni.hideLoading();
			this.addressList = result.data || [];
		},
		async manergeAddress () {
			let self = this;
			if(this.addressStatus) {   // 添加地址
				uni.navigateTo({
					url: '/pages/mine/subPages/addAddress/addAddress',
					animationType: 'slide-in-right'
				})
			}else {                // 删除地址
				let tempArr = [];
				this.addressList.forEach((val, index) => {
					if(val.checked) {
						tempArr.push(val.id)
					}
				})
				if(tempArr.length <= 0) {
					uni.showToast({
						title: '请选择要删除的地址',
						icon: 'none'
					})
					return;
				}
				uni.showModal({
					title: '提示',
					content: '确定要删除吗？',
					success: function (res) {
						if (res.confirm) {
							uni.request({
								method: 'POST',
								url: BASE_URL + '/app/xaddress/addressdelete',
								data: {
									token: uni.getStorageSync('token'),
									data: tempArr
								},
								success: function (result) {
									console.log(result)
									if(result.data.recode == 10000) {
										uni.showToast({
											title: '删除成功'
										})
										self._getAddressList();
									}else {
										uni.showToast({
											title: result.data.remsg,
											icon: 'none'
										})
									}
								}
							})
						}
					}
				});
			}
		},
		radioClick (item) {
			if(item.checked == 'undefined') {
				this.$set(item, 'checked', true)
			}else {
				this.$set(item, 'checked', !item.checked)
			}
		}
	},
	onNavigationBarButtonTap (e) {
		let self = this;
		let pages = getCurrentPages();  
		let page = pages[pages.length - 1];  













	},
	components: {
		noData
	}
}
